.right-wrapper {
  max-width: 200px;
  min-width: 180px;
  margin: 0 auto;
  //   align-items: center;
  //   border: inset 2px rgb(25, 153, 68);
  grid-row: 2;
  grid-column: 5/6;
  background: #edf0ff;
  box-shadow: 10px 0px 20px 0px rgba(0, 0, 0, 0.486);
  border-radius: 0 0 20px 0px;
  border-right: solid 2px rgba(68, 68, 68, 0.45);
  //   border-left: solid 2px rgba(68, 68, 68, 0.45);
  z-index: 2;
}
.right-list {
  //   border: inset 2px blanchedalmond;
  width: 200px;
  // display: flex;
  // justify-content: right;
  li {
    margin: 2%;
  }
  //   right: 0;
}
@media screen and (max-width: 660px) {
  .right-wrapper {
    display: none;
  }
}
