.footer {
  max-width: 1080px;
  // min-width: 640px;
  // margin: 10px auto;
  // margin-top: auto;
  // border: solid 2px white;
  color: rgb(255, 255, 255);
  // position: sticky;
  // bottom: 0px;
  margin: 5px;
  font-size: 12px;
  grid-row: 3/4;
  grid-column: 2/6;

  .contact-real {
    // border: solid 2px wheat;
    display: grid;
    // grid-template-rows: auto minmax(3%, 10%) auto;
    grid-template-columns:
      minmax(30%, auto) minmax(2%, 5%) minmax(27%, auto) minmax(2%, 5%)
      minmax(38%, auto);
    // overflow: hidden;
  }
}
.start {
  border-bottom: inset 2px beige;
  font-size: 16px;

  margin-bottom: 2px;
  // min-width: 140px;
}
.company {
  grid-row: 1/2;
  grid-column: 1/2;
  // min-width: 100px;
}
.sale {
  margin-top: 10px;
}
.buyer {
  grid-row: 1/2;
  grid-column: 3/4;
}
.mail {
  grid-row: 1/2;
  grid-column: 5/6;
  input {
    margin-bottom: 10px;
  }
  a {
    color: rgb(190, 186, 250);
    font-style: italic;
    border-bottom: 1px solid;
  }
}
.link:link,
.link:visited {
  color: rgb(255, 255, 255);
}

.link:hover {
  border-bottom: 1px solid;
}
.my-name {
  grid-row: 2/3;
  grid-column: 1/2;
  // min-height: 50px;
}
.contact-web {
  grid-row: 2/3;
  grid-column: 3/4;
  display: flex;
  // align-self: center;
  // padding: 0px 0px 10px 0px;

  img {
    padding: 0px 15px 10px 0px;
    height: 60px;
    display: block;

    // margin-top: auto;
  }
}
@media screen and (max-width: 700px) {
  .footer {
    font-size: 10px;
  }
  .contact-web {
    img {
      height: 45px;
      // padding: 0px;
    }
  }
}
